import Vue from 'vue';
import dayjs from 'dayjs';

const formatTime = (value , formatString = 'YYYY-MM-DD HH:mm:ss')=> {
    return dayjs(value).format(formatString);
};

const isBefore = (value)=> {
    return dayjs().isBefore(dayjs(value));
};

const isAfter = (value)=> {
    return dayjs().isAfter(dayjs(value));
};

/**
 * base64
 * @param {*} str
 */
const base64Decode = (str)=> {
    if (!str) {
        return str;
    }
    return decodeURIComponent(
        atob(str)
            .split('')
            .map((c)=> '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2))
            .join('')
    );
};

/**
 * base64
 * @param {*} str
 */
const base64Encode = (str)=> {
    if (!str) {
        return str;
    }
    return btoa(
        encodeURIComponent(str).replace(/%([0-9A-F]{2})/g, (match, p1)=> String.fromCharCode('0x' + p1))
    );
};

/**
 * URL
 * @param {*} url
 */
const getAllUrlQuery = (url)=> {
    let _url = url || window.location.href;
    // decodeURI
    let params = decodeURI(_url).split('#')[0].split('?');
    // 
    let result = {};
    if (params.length > 1) {
        params = params[1].split('&');
        params.forEach((item)=> {
            // 
            let arr = item.split('=');
            let key = arr[0];
            let value = arr[1];
            // 2value = 
            // base64
            if (arr.length > 2) {
                arr.splice(0, 1);
                value = arr.join('=');
            }
            // valueJSON.parse(str)
            // valuevaluebase64Encodebase64Decode(str)
            result[key] = value;
        });
    }
    // 
    return result;
};

/**
 * 
 * @param {*} context
 */
const requireAll = context => context.keys().map(item=> {
    return {
        path: item,
        data: context(item)
    };
});

/**
 * cookie
 * @param {*} name
 */
const getCookie = (name) => {
    const reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
    const cookie = document.cookie.match(reg);
    return cookie && cookie[2];
};

/**
 * 
 * @param {*} duration 
 */
const wait = (duration)=> {
    return new Promise(resolve => {
        setTimeout(resolve, duration);
    });
};

/**
 * 
 * @param {*} min 
 * @param {*} max 
 * @param {*} fn Math.floor, Math.ceil
 */
const getRandom = (min, max, fn = (data)=> data)=> {
    return fn(Math.random() * (max - min + 1) + min);
};


const u = navigator.userAgent;
/**
 * 
 */
const isWeixinBrowser = ()=> /micromessenger/i.test(u);
/**
 * Android
 */
const isAndroid = ()=> u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;
/**
 * ios
 */
const isIos = ()=> !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
/**
 * 
 */
const isWide = ()=> window.innerWidth / window.innerHeight >= 0.6;

/**
 * 
 * @param {*} cb
 */
const wxinvoke = (cb)=> {
    // Android
    if (isIos() && isWeixinBrowser()) {
        if (window.WeixinJSBridge) {
            window.WeixinJSBridge.invoke('getNetworkType', {}, cb, false);
        } else {
            let _cb = ()=> {
                document.body.removeEventListener('WeixinJSBridgeReady', _cb, false);
                window.WeixinJSBridge.invoke('getNetworkType', {}, cb);
            };
            document.addEventListener("WeixinJSBridgeReady", _cb, false);
        }
    } else {
        cb();
        // let _cb = ()=> {
        //     document.body.removeEventListener('click', _cb, false);
        //     cb();
        // };
        // document.body.addEventListener('click', _cb, false);
    }
};

const rendeRandom = (min, max)=>{
    let num = Math.random() * max;
    if(num < min) {
        return rendeRandom(min, max);
    }else{
        return num;
    }
};

Vue.$func = Vue.prototype.$func = {
    isBefore,
    isAfter,
    formatTime,
    getAllUrlQuery,
    base64Decode,
    base64Encode,
    requireAll,
    getCookie,
    wait,
    getRandom,
    isWeixinBrowser,
    isAndroid,
    isIos,
    isWide,
    wxinvoke,
    rendeRandom
};

export {
    isBefore,
    isAfter,
    formatTime,
    getAllUrlQuery,
    base64Decode,
    base64Encode,
    requireAll,
    getCookie,
    wait,
    getRandom,
    isWeixinBrowser,
    isAndroid,
    isIos,
    isWide,
    wxinvoke
};
